// Breakpoints use in mixins
$low-mobile-bp: 360px;
$mobile-bp: 1124px;
$laptop-bp: 1600px;

// Colors
$blue: #0082d1;
$white: #fff;
$main-text: #222;
$yellow: #ffd100;

// Flex
@mixin flex-align($align-position) {
  display: flex;
  align-items: $align-position;
}

@mixin flex-justify($justify-position) {
  display: flex;
  justify-content: $justify-position;
}

@mixin flex-justify-align($justify-position, $align-position) {
  display: flex;
  justify-content: $justify-position;
  align-items: $align-position;
}

// Breakpoint mixins
@mixin media-laptop {
  @media (max-width: $laptop-bp) {
    @content;
  }
}

@mixin media-mobile {
  @media (max-width: $mobile-bp) {
    @content;
  }
}

@mixin media-low-mobile {
  @media (max-width: $low-mobile-bp) {
    @content;
  }
}

@mixin floating-animation {
  animation: floating 3s infinite ease-in-out;

  @keyframes floating {
    0% {
      transform: translate(0, 0px);
    }
    50% {
      transform: translate(0, 15px);
    }
    100% {
      transform: translate(0, -0px);
    }
  }
}

@mixin block-padding($val) {
  padding-left: $val;
  padding-right: $val;
}

@mixin title-line {
  border-left: 2px solid $blue;
  padding-left: 10px;
}

@import 'vars';
@import 'mixins';

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 1.1;
  font-weight: 400;
  height: 100%;
  overflow-x: hidden;
  color: $main-text;

  @include media-mobile {
    font-size: 12px;
  }
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
}

#root {
  height: 100%;
}

ul,
ol {
  list-style-type: none;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-button {
  height: 0;
  width: 100%;
}

::-webkit-scrollbar-thumb {
  background-color: $blue;
  border-radius: 50px;
}

::-webkit-scrollbar-track-piece {
  background-color: $main-text;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}
