// Breakpoints use in mixins
$low-mobile-bp: 360px;
$mobile-bp: 1124px;
$laptop-bp: 1600px;

// Colors
$blue: #0082d1;
$white: #fff;
$main-text: #222;
$yellow: #ffd100;

// Flex
@mixin flex-align($align-position) {
  display: flex;
  align-items: $align-position;
}

@mixin flex-justify($justify-position) {
  display: flex;
  justify-content: $justify-position;
}

@mixin flex-justify-align($justify-position, $align-position) {
  display: flex;
  justify-content: $justify-position;
  align-items: $align-position;
}

// Breakpoint mixins
@mixin media-laptop {
  @media (max-width: $laptop-bp) {
    @content;
  }
}

@mixin media-mobile {
  @media (max-width: $mobile-bp) {
    @content;
  }
}

@mixin media-low-mobile {
  @media (max-width: $low-mobile-bp) {
    @content;
  }
}

@mixin floating-animation {
  animation: floating 3s infinite ease-in-out;

  @keyframes floating {
    0% {
      transform: translate(0, 0px);
    }
    50% {
      transform: translate(0, 15px);
    }
    100% {
      transform: translate(0, -0px);
    }
  }
}

@mixin block-padding($val) {
  padding-left: $val;
  padding-right: $val;
}

@mixin title-line {
  border-left: 2px solid $blue;
  padding-left: 10px;
}

.container {
  border: 2px solid $blue;
  border-radius: 8px;
  background-color: $white;
  @include flex-align(flex-end);
  padding: 30px;
  position: relative;
  transition: all .5s ease-in-out;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    z-index: -1;
    top: -2px;
    right: -2px;
    border: 2px solid $blue;
    transition: top .5s ease-in-out, right .5s ease-in-out;
  }
}

.backSide {
  padding: 30px 20px 20px;
  align-items: flex-start;
  transition: all .5s ease-in-out;

  &:after {
    top: -16px;
    right: -16px;
    transition: top .5s ease-in-out, right .5s ease-in-out;

    @include media-mobile {
      top: -8px;
      right: -8px;
    }
  }
}

.title {
  font-size: 30px;
  text-align: right;
  color: $blue;
  max-width: 250px;
  margin-left: auto;

  @include media-mobile {
    font-size: 20px;
    max-width: 220px;
  }
}