// Breakpoints use in mixins
$low-mobile-bp: 360px;
$mobile-bp: 1124px;
$laptop-bp: 1600px;

// Colors
$blue: #0082d1;
$white: #fff;
$main-text: #222;
$yellow: #ffd100;

// Flex
@mixin flex-align($align-position) {
  display: flex;
  align-items: $align-position;
}

@mixin flex-justify($justify-position) {
  display: flex;
  justify-content: $justify-position;
}

@mixin flex-justify-align($justify-position, $align-position) {
  display: flex;
  justify-content: $justify-position;
  align-items: $align-position;
}

// Breakpoint mixins
@mixin media-laptop {
  @media (max-width: $laptop-bp) {
    @content;
  }
}

@mixin media-mobile {
  @media (max-width: $mobile-bp) {
    @content;
  }
}

@mixin media-low-mobile {
  @media (max-width: $low-mobile-bp) {
    @content;
  }
}

@mixin floating-animation {
  animation: floating 3s infinite ease-in-out;

  @keyframes floating {
    0% {
      transform: translate(0, 0px);
    }
    50% {
      transform: translate(0, 15px);
    }
    100% {
      transform: translate(0, -0px);
    }
  }
}

@mixin block-padding($val) {
  padding-left: $val;
  padding-right: $val;
}

@mixin title-line {
  border-left: 2px solid $blue;
  padding-left: 10px;
}

.container {
  @include flex-justify-align(space-between, center);
  @include block-padding(100px);
  padding-bottom: 15px;
  padding-top: 15px;

  @include media-mobile {
    @include block-padding(20px);
  }
}
